import React, { useState, useEffect } from "react"
import styled, { css } from "styled-components"
import AniLink from "gatsby-plugin-transition-link/AniLink"
import "../components/layout.css"

const HeaderNav = styled.header`
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 0 auto;
  padding: 1rem 2rem;
  max-width: 1368px;
`

const HeaderLogo = styled.h1`
  font-size: 36px;
`

const HeaderUL = styled.ul`
  display: none;

  ${props =>
    props.open &&
    props.mobile &&
    css`
      position: fixed;
      background: #1d1d2b;
      top: 0;
      left: 0;
      text-align: center;
      width: 100%;
      height: auto;
      display: block !important;
      z-index: 4;
      padding: 0;
      margin: 0;
    `}

  @media (min-width: 1024px) {
    display: flex;
  }
`

const HeaderLI = styled.li`
  margin: 0 2rem;
  list-style-type: none;
  font-size: 18px;
  letter-spacing: 1px;

  ${props =>
    props.open &&
    props.mobile &&
    css`
      margin: 3rem;
    `}
`

const HeaderMobile = styled.div`
  cursor: pointer;
  z-index: 99;

  @media (min-width: 1024px) {
    display: none;
  }
`

function useWindowSize() {
  const [windowSize, setWindowSize] = useState({
    width: undefined,
    height: undefined,
  })
  useEffect(() => {
    // Handler to call on window resize
    function handleResize() {
      // Set window width/height to state
      setWindowSize({
        width: window.innerWidth,
        height: window.innerHeight,
      })
    }
    // Add event listener
    window.addEventListener("resize", handleResize)
    // Call handler right away so state gets updated with initial window size
    handleResize()
    // Remove event listener on cleanup
    return () => window.removeEventListener("resize", handleResize)
  }, []) // Empty array ensures that effect is only run on mount
  return windowSize
}

const Header = () => {
  const size = useWindowSize()
  const [isOpen, setIsOpen] = useState(false)

  return (
    <HeaderNav>
      <HeaderLogo>
        <AniLink paintDrip duration={0.4} hex="#212840" to="/">
          MD
        </AniLink>
      </HeaderLogo>
      <nav>
        <HeaderUL mobile={size.width < 1024} open={isOpen}>
          <HeaderLI mobile={size.width < 1024} open={isOpen}>
            <AniLink paintDrip duration={0.4} hex="#212840" to="/">
              Home
            </AniLink>
          </HeaderLI>
          <HeaderLI mobile={size.width < 1024} open={isOpen}>
            <AniLink paintDrip duration={0.4} hex="#212840" to="/about/">
              About
            </AniLink>
          </HeaderLI>
          <HeaderLI mobile={size.width < 1024} open={isOpen}>
            <AniLink paintDrip duration={0.4} hex="#212840" to="/skills/">
              Skills
            </AniLink>
          </HeaderLI>
          <HeaderLI mobile={size.width < 1024} open={isOpen}>
            <AniLink paintDrip duration={0.4} hex="#212840" to="/projects/">
              Projects
            </AniLink>
          </HeaderLI>
          <HeaderLI mobile={size.width < 1024} open={isOpen}>
            <AniLink paintDrip duration={0.4} hex="#212840" to="/contact/">
              Contact
            </AniLink>
          </HeaderLI>
        </HeaderUL>
      </nav>
      <HeaderMobile onClick={() => setIsOpen(!isOpen)}>
        <svg
          xmlns="http://www.w3.org/2000/svg"
          className="icon icon-tabler icon-tabler-menu"
          width="44"
          height="44"
          viewBox="0 0 24 24"
          strokeWidth="1.5"
          stroke="#ffffff"
          fill="none"
          strokeLinecap="round"
          strokeLinejoin="round"
        >
          <path stroke="none" d="M0 0h24v24H0z" fill="none" />
          <line x1="4" y1="8" x2="20" y2="8" />
          <line x1="4" y1="16" x2="20" y2="16" />
        </svg>
      </HeaderMobile>
    </HeaderNav>
  )
}

export default Header
